.weather{
  width: 100%;
  color:red;
  font-weight: 900;
  position: absolute;
  font-size: 40px;
  line-height: 0px;
  bottom:00px;
  left: 00px;
  margin: 0 auto 0 auto;
}


.smartarse.night .weather{
  color:#8379f4;
}

.forecast{
  float: left;
  color:;
  color:#858585;
  width: 80px;
  padding: 10px 26px 0;
}

  .forecast p{
    float:left;
    margin: 0;
    font-size: 12px;
    margin: 23px 0 0 1px;
    text-align: left;
  }

  .forecast p.temp{
    margin: 15px 0 0 1px;
  }

  .forecast .icon{
    float:left;
    font-size: 45px;
    padding: 5px 0 15px 0;
  }

  .forecast.Thunderstorm{
    color: rgb(255, 255, 255);
  }
  
  .forecast.Rain{
    color: rgb(43, 95, 155);
  }
  
  .forecast.Drizzle{
    color: rgb(88, 114, 143);
  }
  
  .forecast.Snow{
    color: #fff;
  }

  .forecast.Clear{
    color: #FE9700;
  }

  .forecast.Clouds{
    color: rgb(223, 213, 213);
  }



.calendar{
  height: 40px;
  padding: 30px 0 0px 0;
  color:#ff5722;
  font-size: 38px;  
  font-family: 'Amatic SC', cursive;
  font-weight: 770;
  width: 100%;
  position: absolute;
  bottom: 130px;
  text-shadow: 0px 0px 1px #ff5722;
}

.calendar svg{
  font-size: 26px;
}

.calendar-left{
  width: 357px;
  font-weight: 400;
  text-align: center;
  margin-left: 40px;
  float:left;
}

.calendar-right{
  width: 397px;
  font-weight: 400;
  text-align: center; 
  float:right;
}

html,body{
  overflow:hidden;
  background-color: #fff;
  font-weight: bold;
}

.App{
  font-family: 'Yellowtail', cursive;
}

.smartarse {
  background-color: #000;
  position: relative;
  width: 800px;
  height: 480px;
  margin: 0 auto 0 auto;
}

.clock{
  width: 800px;
  height: 100px;
  position: absolute;
  top: 60px;
  left: 0px;
  padding: 30px 0 30px 0;
  text-align: center;
}

h1.time{
  color:#ffa000;
  font-size: 280px;
  font-weight: 700;
  line-height: 100px;
  margin: 0 auto 0 auto;
  padding:0;
}

  .smartarse.night h1.time,
  .smartarse.night .weather,
  .smartarse.night .calendar{
    opacity: 0.3;
  }
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  cursor: none;
}

